*,body{
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
    outline: none;
    color: inherit;
    transition: all 0.5s;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.EmojiApp{
    height: auto;
    display: grid;
    grid-template-columns: 0.99fr;
    grid-template-rows: 20vh 10vh auto;
    align-content: space-around;
    justify-content: space-evenly;
}

.EmojiApp>h1{
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 3rem;
    color: #444444;
    letter-spacing: 2px;
}

.EmojiApp>input{
    font-weight: normal;
    font-size: 1.2rem;
    color: #757757;
    border-radius: 5px;
    padding-left: 10px;
    border: 1px solid rgba(0, 0, 0, 0.5);
}

.EmojiApp>input::placeholder{
    font-weight: bold;
    font-size: 1.2rem;
    letter-spacing: 2px;
}

.EmojiApp>ul{
    height: auto;
    display: grid;
    grid-template-columns: 1fr;
    margin-top: 5vh;
}

.EmojiApp>ul>li{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 20px;
    font-weight: normal;
    font-size: 1.2rem;
    color: #323232;
    border: 1px solid rgba(0, 0, 0, 0.3);
    border-bottom: none;
}

.EmojiApp>ul>li>p{
    padding-left: 10px;
}

@media (min-width:150px) and (max-width:500px)
{
    .EmojiApp>h1{
        font-weight: normal;
        font-size: 2.5rem;
    }   

    .EmojiApp>input{
        font-weight: normal;
        font-size: 1.1rem;
    }

    .EmojiApp>input::placeholder{
        font-weight: bold;
        font-size: 1.1rem;
        letter-spacing: 1px;
    }
}